@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@600&display=swap');

.hints{

    background-color: #175A24;
    width: 100px;
    color: rgb(226, 136, 17);
   
}

.Cart{
    position: absolute;
    color: rgb(226, 136, 17);
    bottom: 0;
    top: 200px;
    left: 30px;
    font-size: 30px;
    font-weight: 100;

    
}
.balans{
    position: absolute;
    color: rgb(228, 228, 228);
    bottom: 0;
    top: 250px;
    left: 30px;
    font-size: 30px;
    font-weight: 100;
    font-family: 'Inconsolata', monospace;

}
.travel{
   width: 100px;
   background: none;
   border: none;
   cursor: pointer;
   color: rgb(255, 255, 255);
   font-size: 20px;
   font-family: 'Arial', monospace;
   border-radius: 2px;
   background-color: rgb(226, 136, 17);
   -webkit-border-radius: 2px;
   -moz-border-radius: 2px;
   -ms-border-radius: 2px;
   -o-border-radius: 2px;
   position: absolute;
   bottom: 0;
   right: 0;
}

.Hidden {
    overflow: hidden;
}

.balansInput{
    position: absolute;
    color: rgb(228, 228, 228);
    bottom: 0;
    top: 0px;
    left: -2px;
    font-size: 30px;
    font-weight: 100;
    font-family: 'Inconsolata', monospace;
    background: none;
    border: none;
    width: 60px;

}
.balansInputWrapper{
    position: relative;
    color: rgb(228, 228, 228);
    bottom: 0;
    top: 250px;
    left: 30px;
    font-size: 30px;
    font-weight: 100;
    font-family: 'Inconsolata', monospace;
    background: none;
    border: none;
}
.balansInputWrapper span{
    position: relative;
    color: rgb(228, 228, 228);
    bottom: 0;
    top: 0px;
    left: 60px;
    
}