.wraper_Etiket__Page{
  background-color: #1C1D22;
  min-height: 100vh;
}
.wraper_Etiket{
  
  display: grid;
  justify-content: center;
  align-content: center;
  font-family: 'Roboto', sans-serif;
  
}


.Etiket{
  margin-top: 17%;
  background: #175A24;
  width: 550px;
  height: 300px;
  border-radius:10px ;
  -webkit-border-radius:10px ;
  -moz-border-radius:10px ;
  -ms-border-radius:10px ;
  -o-border-radius:10px ;
  position: relative;
  
}

.Etiket__text{
  position: absolute;
  font-size: 50px;
  top: 50%;
  font-weight: 900;
  transform: translate(0,-50%);
  right: 150px;
  
  -webkit-transform: translate(0,-50%);
  -moz-transform: translate(0,-50%);
  -ms-transform: translate(0,-50%);
  -o-transform: translate(0,-50%);
  color: #fff;
}

.Etiket__input{
  position: absolute;
  width: 400px;
  top: 240px;
  transform: translate(0,-50%);
  left: 50px;
  height: 20px;
  font-size: 18px;
  text-transform: uppercase;
  
  font-weight: 400;
  border: 1px solid #fff ;
  -webkit-transform: translate(0,-50%);
  -moz-transform: translate(0,-50%);
  -ms-transform: translate(0,-50%);
  -o-transform: translate(0,-50%);
  border-radius:10px ;
  -webkit-border-radius:10px ;
  -moz-border-radius:10px ;
  -ms-border-radius:10px ;
  -o-border-radius:10px ;
  background-color: #175a24;
  color: rgb(226, 136, 17);
}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus{
  -webkit-text-font-size: 15px;
  -webkit-text-fill-color: rgb(226, 136, 17);
 
  -webkit-transition: background-color 5000s ease-in-out 0s;
 
  transition: background-color 5000s ease-in-out 0s;
}
.Etiket__search{
  width: 160px;
 max-height: 24px;
  position: absolute;
  right: 0;
  bottom: 0px;
  border-radius:10px ;
  border:none;
  background-color: #175a24;
  border-radius:10px     ;
  -webkit-border-radius:10px ;
  -moz-border-radius:10px ;
  -ms-border-radius:10px ;
  -o-border-radius:10px ;
  font-family: 'Oswald', sans-serif;
  text-align: center;
  padding-bottom: 30px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.Etiket__search:hover{
  background-color: #22222D;
  
  
}

.Etiket__search span{
  color: #fff;
  font-weight: 500;
  font-size: 20px;

  
}
@media screen and (max-width: 550px) {
 .Etiket{
  margin-top: 4%;
   transform: scale(0.8);
   -webkit-transform: scale(0.8);
   -moz-transform: scale(0.8);
   -ms-transform: scale(0.8);
   -o-transform: scale(0.8);
}
}
@media screen and (max-width: 440px) {
  .Etiket{
    
    transform: scale(0.7);
    -webkit-transform: scale(0.7);
    -moz-transform: scale(0.7);
    -ms-transform: scale(0.7);
    -o-transform: scale(0.7);
}
 }
 @media screen and (max-width: 390px) {
  .Etiket{
    transform: scale(0.6);
    -webkit-transform: scale(0.6);
    -moz-transform: scale(0.6);
    -ms-transform: scale(0.6);
    -o-transform: scale(0.6);
}
 }
.notification__Editmod{
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgb(29, 179, 179);
  width: 100%;
  height: 18px;
  border-radius:3px;
  -webkit-border-radius:3px;
  -moz-border-radius:3px;
  -ms-border-radius:3px;
  -o-border-radius:3px;
  visibility: visible;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  color: #fff;
  font-weight: 100;
  
}
.notification__Editmod span{
  width: 100%;
  
}
