.preloader{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
}
.Hidden {
    display: none;
}
.preloaderMini{
    position: absolute; 
    
}
.preloaderMini img{
    
    width: 60px;
    height: 60px;
}
